.profile__info {
    display: flex;
    flex-direction: row;
    column-gap: 18px;
    margin-top: 22px;
}

@media screen and (max-width: 1070px) {
    .profile__info {
        max-width: 100%;
        margin: 26px auto 0;
        column-gap: 10px;
        transform: translateX(14px);
    }
}
